// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// 引入初始化样式
import '@/assets/css/reset.css'

import $ from 'jquery'
import axios from 'axios'

// import '@/assets/css/font-awesome-5.4.1.css'
// import '@/assets/css/font-awesome-4.7.0.css'
// 引入materialize
import 'materialize-css/dist/css/materialize.css'
// import 'materialize-css/dist/js/materialize.js'

// 引入element
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

Vue.prototype.goNext = function (e) {
  var speed = 500
  if (this.$route.path != '/') {
    this.$router.push('/')
    speed = 0
  }
  setTimeout(function () {
    if (e === 'home') {
      $('html , body').animate({
          scrollTop: $('#welcome').offset().top - 50
        },
        speed
      )
    } else if (e === 'article') {
      $('html , body').animate({
          scrollTop: $('.articleList').offset().top - 280
        },
        speed
      )
    } else if (e === 'project') {
      $('html , body').animate({
          scrollTop: $('#projects').offset().top - 60
        },
        speed
      )
    }
  }, 100)
}
Vue.prototype.loveArtilce = function (e, event) {
  if (this.youHaveLoved.indexOf(e._id) == '-1' && this.isClick) {
    // 点击开关，当点击一次时开关关闭，在完成动画后开关打开，避免在动画执行期间重复点击
    this.isClick = !this.isClick
    e.love++
    $(event.target).addClass("heartAnimation")
    var that = this
    // 给点击动画预留的时间缓冲
    setTimeout(function () {
      that.youHaveLoved.push(e._id)
      axios
        .get('/api/loveArtilce', {
          params: {
            id: e._id
          }
        })
      localStorage.setItem("youHaveLoved", JSON.stringify(that.youHaveLoved))
      that.isClick = !that.isClick
    }, 800)
  } else {
    this.$notify({
      title: '调皮😝',
      message: '你已经点过赞啦～',
      type: 'warning'
    })
  }
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



