<template>
  <div
    class="markdown-body"
    v-loading="false"
  >
    <h3> {{ article.title }}</h3>
    <div
      class="info"
      v-if="article.date"
    >
      <span class="date"><i class="fa fa-calendar"></i>{{article.date}}</span>
      <span class="tag"><i class="fa fa-tags"></i>{{article.tag}}</span>
      <!-- <span class="comment"><i class="fa fa-comment"></i>评论 ( {{article.comment}} )</span> -->
      <span class="views"><i class="fa fa-eye"></i>浏览 ( {{article.view}} )</span>
      <span class="loveThis">
        <span
          class="heart"
          :class="{'heartActive':(youHaveLoved.indexOf(article._id) != '-1')}"
          @click="loveArtilce(article,$event)"
        ></span>
        <span>喜欢 ( {{article.love}} )</span>
      </span>
    </div>
    <div
      v-html="article.content"
      v-highlight
      v-transImgBase64="{ img : article.img}"
    ></div>
    <div
      class="reward"
      @click="rewardArticle"
    >
      <span>打赏一个</span>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import axios from 'axios'
import hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'

export default {
  data () {
    return {
      article: '',
      loading: '',
      youHaveLoved: JSON.parse(localStorage.getItem("youHaveLoved")) || [],
      isClick: true,
    }
  },
  methods: {
    getArticle () {
      axios
        .get('/api/getArticle/' + this.$route.params.id)
        .then(res => {
          // console.log(this.article)
          this.article = res.data
          this.article.view = this.article.viewer.length
          this.article.love = this.article.lover.length
          document.title = this.article.title
        })
    },
    rewardArticle () {
      this.$alert("<img src='/images/reward.png' style='width:100%'>", '感谢你的支持😊', {
        dangerouslyUseHTMLString: true,
        showConfirmButton: false,
        callback: action => { }
      });
    },
    articleMinHeight () {
      var articleMinHeight = $(window).height() - $('footer').height() + 'px'
      $('.markdown-body').css('min-height', articleMinHeight)
    }
  },
  directives: {
    highlight (el) {
      let blocks = el.querySelectorAll('pre code')
      blocks.forEach((block) => {
        hljs.highlightBlock(block)
      })
    },
    transImgBase64 (el, binding) {
      let blocks = el.querySelectorAll('img')
      let imgList = binding.value.img
      blocks.forEach((block) => {
        let el = (imgList.find((element) => (element.name == block.getAttribute('src').replace(/img\//, ""))))
        block.setAttribute("src", el.file)
      })
    }
  },
  mounted () {
    this.getArticle()
    this.articleMinHeight()
    window.onresize = this.articleMinHeight
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 800px) {
  .markdown-body {
    padding: 100px 5% !important;
  }
}
.markdown-body {
  position: relative;
  background-color: white;
  padding: 100px 20%;
  z-index: 1;
  .info {
    font-size: 0.8em;
    color: #857d7d;
    span {
      display: inline-block;
      margin-right: 10px;
      margin-bottom: 10px;
    }
    span i {
      margin-right: 3px;
    }
    .tag {
      color: #e73da0;
    }
    .loveThis {
      color: #6c6d6b;
      .heart {
        cursor: pointer;
      }
    }
    .views {
      color: #6c6d6b;
    }
    .comment {
      color: #6c6d6b;
      cursor: pointer;
    }
    .comment:hover {
      text-decoration: underline;
    }
    .fa-calendar {
      color: #21bfd4;
    }
  }
  .reward {
    font-size: 16px;
    font-weight: 900;
    cursor: pointer;
    color: #e73da0;
    margin-top: 50px;
    text-align: center;
    span {
      padding: 5px;
      border: 1px solid #e73da0;
      border-radius: 5px;
      transition: 300ms;
      &:hover {
        background-color: #e73da0;
        color: #fff;
        transition: 300ms;
      }
    }
  }
}
</style>



