<template>
  <section id="projects">
    <div class="projects-border"></div>
    <div class="myWorks">
      <p>My Projects</p>
    </div>
    <div
      class="project"
      v-for="(project,index) in projects"
      :key="index"
    >
      <div class="projectImg">
        <img
          :src="project.img"
          alt=""
        >
      </div>
      <div class="projectIntroduction">
        <div class="projectTitle">
          <p class="projectIcons">
            <i
              class="fab"
              v-for="icon in project.icons"
              :key="icon"
              :class="icon"
            ></i>
          </p>
          <p class="projectName">
            {{ project.title }}
          </p>
        </div>
        <div class="projectDetail">
          {{ project.content }}
        </div>
        <div class="projectButtons">
          <a
            class="btn-1"
            :href="project.button1.url"
          >{{ project.button1.lable }}</a>
          <a
            class="btn-2"
            :href="project.button2.url"
          >{{ project.button2.lable }}</a>
        </div>
      </div>
    </div>
    <!-- 分页按钮 -->
    <el-pagination
      class="pagination"
      layout="prev, pager, next"
      :page-count="projectsListLength"
      :current-page="currentPage"
      @current-change="handleCurrentChange"
    >
    </el-pagination>
  </section>
</template>

<script>
import axios from 'axios'
import $ from 'jquery'

export default {
  data () {
    return {
      projects: '',
      projectsListLength: 1,
      currentPage: 1,
      projectsPerPage: 3
    }
  },
  methods: {
    resize: function () {
      $('.projects-border').css({
        'border-left-width': $(window).width() * 0.8 + 'px',
        'border-right-width': $(window).width() * 0.2 + 'px'
      })
    },
    getProjectList: function () {
      axios
        .get('/api/getProjectList', {
          params: {
            currentPage: this.currentPage,
            projectsPerPage: this.projectsPerPage
          }
        })
        .then(res => {
          this.projectsListLength = Math.ceil(res.data.length / this.projectsPerPage)
          this.projects = res.data.projects
        })
    },
    handleCurrentChange: function (val) {
      this.currentPage = val
      this.getProjectList()
    }
  },
  mounted () {
    $(window).on('resize', this.resize)
    this.resize() // 初始化需要js计算的宽高
    this.getProjectList()
  }
}
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 669px) {
  #projects {
    padding: 10px 20px !important;
  }
  .project {
    display: flex;
    flex-direction: column;
  }
  .projectImg {
    width: 80% !important;
    padding-bottom: 80% !important;
  }
  .projectIntroduction {
    position: relative !important;
    width: 80% !important;
    right: 0 !important;
    margin-top: 10px;
    margin-left: 0 !important;
  }
}

#projects {
  position: relative;
  width: 100%;
  padding: 90px 20%;
  background-color: #fff;
  z-index: 1;
  .projects-border {
    position: absolute;
    top: -179px;
    left: 0px;
    padding: 0;
    border-bottom: 90px solid #fff;
    border-top: 90px solid #f6f6f6;
    border-left: solid #f6f6f6;
    border-right: solid #f6f6f6;
    display: block;
    content: "";
    z-index: 1;
  }
  .myWorks {
    text-align: center;
    margin-bottom: 50px;
    p {
      display: inline-block;
      font-size: 2.5em;
      font-weight: 800;
      padding-bottom: 10px;
      border-bottom: 5px solid #f300b4;
    }
  }
  .project {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    // background-color: #ccc;
    margin-bottom: 80px;
    .projectImg {
      position: relative;
      width: 45%;
      padding-bottom: 45%;
      border-radius: 50%;
      margin: auto 0;
      border: 1px solid #fff;
      box-shadow: 0 20px 10px -10px #25293450;
      overflow: hidden;
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        max-width: 100%;
        filter: saturate(0);
        border-radius: 50%;
        transition: 300ms;
        &:hover {
          transform: scale(1.1);
          filter: none;
          transition: 300ms;
        }
      }
    }
    .projectIntroduction {
      margin-left: 10%;
      width: 45%;
      //   cursor: pointer;
      .projectTitle {
        .projectIcons i {
          color: #181818;
          letter-spacing: 0.2em;
          font-size: 1.5em;
          margin-bottom: 20px;
          margin-left: 5px;
        }
        .projectName {
          font-size: 2em;
          font-weight: 500;
          font-style: italic;
          color: #f300b4;
        }
      }
      .projectDetail {
        font-size: 1.2em;
        line-height: 1.3em;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        margin: 10px auto;
      }
      .projectButtons {
        a {
          display: inline-block;
          width: 49%;
          font-size: 1.2em;
          padding: 10px 5px;
          text-align: center;
          margin-top: 10px;
          color: #252934;
          background-color: #e3e3e3;
          border-bottom: 1px solid #f300b4;
          cursor: pointer;
          transition: 300ms;
          &:hover {
            background-color: #f300b4;
            color: #fff;
            transition: 300ms;
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>



